@import "./../../libs/variables.scss";

.post {
    position: relative;
    border-radius: 10px;
    padding: 20px;
    box-shadow: 0px 0px 10px $ligthgrey;

    h3 {
        margin-top: 0;
    }

    img {
        width: 100%;
    }

    .deleteBtn {
        position: absolute;
        right: 0;
        margin-right: 20px;

        background: transparent;
        border: 0;
        color: red;
        font-weight: bold;
        font-size: 2rem;
        cursor: pointer;
    }
}